import { RouterModule, Routes } from '@angular/router';

import { HomeComponent }  from './home/home.component';
import { AboutComponent } from './about/about.component';

import { RouteGuard }      from './shared';

// TODO: implement these routes
const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'org/profile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+organizer/profile.module')['OrganizerProfileModule']);  });}) },
  { path: 'xhb/profile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+exhibitor/profile.module')['ExhibitorProfileModule']);  });}) },
  { path: 'adm/profile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+administrator/profile.module')['AdminProfileModule']);  });}) },
  { path: 'notloggedin', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+notloggedin/login.module')['LoginModule']);  });}) },
  { path: 'join/:portal', canActivate: [RouteGuard], loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+join/join.module')['JoinModule']);  });}) },
  { path: 'about', component: AboutComponent}
];

export const routing = RouterModule.forRoot(routes);



